@keyframes slide-in-top {
  0% {
    transform: translateY(-200px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slide-in-bottom {
  0% {
    transform: translateY(200px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.manual-lead-processing {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 40px;
  font-weight: 500;
  opacity: 0;
}
.manual-lead-processing.animate {
  animation: 1s ease 0s 1 normal forwards slide-in-top;
}
.the-sales-team {
  margin: 0;
}
.the-sales-team-container {
  position: absolute;
  top: 73px;
  left: 0;
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  display: inline-block;
  width: 1154px;
  opacity: 0;
}
.allen-gooch-general-container.animate,
.group-child.animate,
.the-sales-team-container.animate,
.we-would-have-container.animate {
  animation: 1s ease 0s 1 normal forwards slide-in-bottom;
}
.manual-lead-processing-creates-parent {
  position: absolute;
  top: 83px;
  left: 122px;
  width: 1154px;
  height: 289px;
}
.group-wrapper {
  position: absolute;
  top: calc(50% - 389px);
  left: calc(50% - 700px);
  width: 1400px;
  height: 447px;
}
.group-child,
.we-would-have-container {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.group-child {
  border-radius: 20px;
  background-color: #efeff1;
  width: 1154px;
  height: 331px;
}
.we-would-have-container {
  line-height: 40px;
}
.allen-gooch-general-container {
  position: absolute;
  top: 162px;
  left: 402px;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  color: #18003b;
  opacity: 0;
}
.we-would-have-our-best-salespe-parent {
  position: absolute;
  top: 66px;
  left: 76px;
  width: 1000px;
  height: 198px;
}
.rectangle-parent {
  position: absolute;
  top: 0;
  left: 121px;
  width: 1154px;
  height: 331px;
}
.group-container {
  position: absolute;
  top: calc(50% + 58px);
  left: calc(50% - 700px);
  width: 1400px;
  height: 331px;
  text-align: center;
  font-size: 28px;
  color: #4e2886;
}
.frame-parent {
  position: relative;
  background-color: #fff;
  width: 100%;
  height: 790px;
  cursor: pointer;
  text-align: left;
  font-size: 36px;
  color: var(--color-black);
  font-family: var(--font-raleway);
}
